#buttons-wrapper{margin:0 auto;margin-top:3rem;text-align:center}button{margin-left:0.5rem;margin-right:0.5rem;padding:5px 10px;font-family:"Nunito";font-size:1.3rem;background-color:#838383;color:white;border:none;border-radius:8px;cursor:pointer}button:hover{-webkit-filter:brightness(50%);filter:brightness(50%)}.strict-on{background-color:#cc0f0f;color:white}.strict-disabled{pointer-events:none;-webkit-filter:brightness(70%);filter:brightness(70%)}#circles-wrapper{width:100%;margin:0 auto;margin-top:5rem;display:-ms-grid;display:grid;-ms-grid-columns:1fr 1fr 1fr;grid-template-columns:1fr 1fr 1fr;-ms-grid-rows:1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-gap:2rem;justify-content:center;background-color:#e6e6e6}.color-circle{width:100%;height:100%;-ms-grid-row-align:center;align-self:center;-ms-grid-column-align:center;justify-self:center;border-width:4px;border-style:solid;border-radius:50%;cursor:pointer}#red-circle{-ms-grid-column:2;-ms-grid-column-span:1;grid-column:2 / 3;-ms-grid-row:3;-ms-grid-row-span:1;grid-row:3 / 4;background-color:#f46256;border-color:#c1272d}#green-circle{-ms-grid-column:1;-ms-grid-column-span:1;grid-column:1 / 2;-ms-grid-row:2;-ms-grid-row-span:1;grid-row:2 / 3;background-color:#93dd99;border-color:#00be00}#blue-circle{-ms-grid-column:2;-ms-grid-column-span:1;grid-column:2 / 3;-ms-grid-row:1;-ms-grid-row-span:1;grid-row:1 / 2;background-color:#94caf2;border-color:#4466e2}#yellow-circle{-ms-grid-column:3;-ms-grid-column-span:1;grid-column:3 / 4;-ms-grid-row:2;-ms-grid-row-span:1;grid-row:2 / 3;background-color:#fff96a;border-color:#bfaa34}.active-circle{-webkit-filter:brightness(50%);filter:brightness(50%)}#middle-circle{width:60px;height:60px;-ms-grid-column:2;-ms-grid-column-span:1;grid-column:2 / 3;-ms-grid-row:2;-ms-grid-row-span:1;grid-row:2 / 3;align-self:center;justify-self:center;display:flex;flex-direction:column;justify-content:center;align-items:center;color:black;border:2px solid black;border-radius:50%}#round-display{font-family:"Nunito";font-size:1.5rem;font-weight:500}@media only screen and (min-width: 300px){#circles-wrapper{width:280px;height:270px}}@media only screen and (min-width: 350px){#circles-wrapper{width:300px;height:290px}}@media only screen and (min-width: 400px){#circles-wrapper{width:330px;height:315px}}@media only screen and (min-width: 500px){#circles-wrapper{width:370px;height:350px}}@media only screen and (min-width: 600px){#circles-wrapper{width:400px;height:390px}}@media only screen and (min-width: 1200px){.color-circle{width:130px;height:130px}}body{background-color:#e6e6e6}#app{background-color:#e6e6e6;width:100%}h1{text-align:center;font-family:"Nunito";margin-top:1.8rem}.disabled{pointer-events:none}

